@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

*{
    box-sizing: border-box;
}

body {
    font-family: 'Inter', sans-serif; 
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
    min-height: 100vh;
    margin: 0;
}

h3 {
    margin: 10px 0;
}

h6 {
    margin: 5px 0;
    text-transform: uppercase;
}

p {
    font-size: 16px;
    line-height: 21px;
    text-align: center;
}
.card-container{
    background-color: #353b48;
border-radius: 5px;
box-shadow: 0px 10px 20px -10px rgba(0,0,0.76);
color: #b3b8cd;
padding-top: 30px;
width: 350px;
max-width: 100%;
text-align: center;
}

.pro{
    font-weight: bold;
    color: #231e39;
    border-radius: 3px;
    font-size: 15px;
    background: red;
    padding: 3px 7px;
}

.online{
    background-color: rgb(9, 185, 9);
}
.offline{
    background-color: #febb0b;
}
.img{
    border: 3px solid whitesmoke;
    border-radius: 50%;
}
.buttons{
    display: flex;
    justify-content: center;
    gap: 10px;
}

button.primary{
    background-color: #0984e3;
    border: 2px solid #0984e3;
    border-radius: 3px;
    color: #fff;
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    padding: 10px 25px;
}
button.primary.outline{
background-color: transparent;
}
.skills{
    background-color: #2f3640;
    text-align: left;
    padding: 15px;
    margin-top: 15px;
}
.skills ul{
    text-decoration: none;
    list-style-type: none;
    margin: 0;
    padding: 0;    
}
.skills ul li{
    border: 1px solid #595180;
    display: inline-block;
    border-radius: 2px;
    padding: 7px;
    margin: 0 7px 7px 0;
    font-size: 12px;

}